.card {
  background: #fff;
  border-radius: 2px;
  height: 325px;
  margin: 1rem;
  position: relative;
  width: 325px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .card-img {
  overflow: hidden;

}

.protitle {
  position: absolute;
  text-align: center;
  bottom: 15px;
  width: 100%;
  padding: 15px;
  background-color: #111111 ;
  color: #ffffff;
  cursor: pointer;
}

.card:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.jumbotron {
    margin-top: 54px;
}
.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

